@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

@font-face {
  font-family: "NM";
  src: url("../fonts/NeueMachina-Regular.otf");
  font-weight: 400;
}

@font-face {
  font-family: "NM";
  src: url("../fonts/NeueMachina-Light.otf");
  font-weight: 300;
}

@font-face {
  font-family: "NM";
  src: url("../fonts/NeueMachina-Ultrabold.otf");
  font-weight: 700;
}

html {
  margin: 0;
  padding: 0;
  min-height: 100%;
  box-sizing: border-box;
  font-family: "NM";
  letter-spacing: -0.03em;
}

body {
  margin: 0;
  padding: 0 9.5vw;
  min-height: 100%;
  height: 100%;
  box-sizing: border-box;
  background-color: #1d1d1d;
}

.sticky-nav {
  display: flex;
  justify-content: space-between;
  padding-top: 6vw;
  box-shadow: 0 0 7px 0px black;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 6vw 9.5vw 3vw 9.5vw;
  background-color: #1d1d1d;
}

.srinivas-teja {
  display: inline;
}

.bigboi {
  color: white;
  font-size: 4.3vw;
  font-weight: 300;
  margin: 0;
  padding: 0;
}

.sub-bigboi {
  color: white;
  margin: 0;
  font-family: Inter;
  font-size: 3vw;
  font-weight: 300;
  opacity: 0.5;
}

.hamburger {
  color: white;
  font-size: 7vw;
}

.Hero-1 {
  margin-top: 25vw;
}

.lead {
  color: white;
  margin: 0;
  font-size: 13vw;
  font-weight: 700;
  line-height: 1;
}

.hero1-srini {
  width: 100%;
  margin-top: 7vw;
}

hr {
  display: block;
  color: white;
  width: 7vw;
  height: 1px;
  margin: 0;
  border: 0;
  border-top: 3px solid white;
  margin: 3vw 0;
}

.sublead {
  color: white;
  font-size: 8.5vw;
  line-height: 1;
}

.description {
  color: white;
  font-weight: 300;
  line-height: 1.8em;
  font-size: 3.5vw;
}

a {
  color: white;
}
